<template>
	<view>
    </view>
</template>
<script>
var _self;
export default {
	data() {
		return{
			status:0, 
		}
	},
	onShow() {
		if(_self.status == 1){
			uni.switchTab({
			    url: '/pages/index/index',
				success: function (res) {
					_self.status = 0;
				}
			});
		}else{
			uni.navigateTo({
			    url: '/pages/recharge/recharge',
				success: function (res) {
					_self.status = 1;
				}
			});
		}
		
	},
	onLoad(e) {
		_self = this;
		uni.navigateTo({
		    url: '/pages/recharge/recharge',
			success: function (res) {
				_self.status = 1;
			}
		});
	},


};
</script>
